.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 50;
    min-height: 70px;
    border-bottom: 10px solid transparent;
    background-color: #000;
}

.header__row {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    padding: 0 30px;
    justify-content: space-between;
    align-items: center;
}
@media screen and (min-width: 767px){
    .menu__list{
        display: flex;
        align-items: center;
        & #cont{
            text-decoration: underline;
        }
        & > li{
    
            padding: 10px 0;
        }
    
    }
}
