.menu__list {
    display: flex;
    flex-wrap: wrap;
    padding: 0 0 0 15px;

    &>.itemsElements {
        position: relative;
        margin: 20px 0 0 20px;
    }
}

.itemsElements {
    color: #fff;
    font-size: 18px;
    margin: 0 20px 0 0;
    letter-spacing: 0.1em;

    &:hover {
        color: rgba(255, 166, 0, 0.795);
        text-decoration: underline;
    }
}
